/*Calendar day container*/
.block-container {
    height:150px;
    width: 50%;
    margin: 10px auto;
    background-color: #EEE;
    text-align: center;
    position: relative;
    display: grid;
}

.calendar-block {
    grid-row: 1;
    grid-column: 1;

}

.old-calendar-block {
    grid-row: 1;
    grid-column: 1;
}

.calendar-block-animate {
    height:150px;
    background:#EEE;
    opacity: 0;
    -webkit-animation: calendarRoll .7s ease;
            animation: calendarRoll .7s ease;
}

.calendar-block h3 {
    padding-top:5px;
    color:white;
    font-weight: bold;
    font-size: 2.2rem;
    background: -webkit-linear-gradient(left, #333333, #dd1818);
    background: linear-gradient(to right, #333333, #dd1818);
}

.calendar-block .day-paragraph, .calendar-block-animate .day-paragraph {
    background-color: white;
    font-weight: bold;
    width: 30%;
    margin: 15px auto 20px;
    border-radius:15px;
    font-size: 2.2rem;
}

/*Button for saving day to my days*/
.save-note > button {
    margin-top:15px;
    width: 100%;
}
/*Holes in calendar*/
.calendar-block:before, .calendar-block:after {
    content: '';
    float: left;
    position: absolute;
    top: 5px;
    width: 8px;
    height: 8px;
    background: #111;
    z-index: 1;
    border-radius: 10px;
    box-shadow: 0 1px 1px #fff;
}

.calendar-block:before {
    left: 11px;
}

.calendar-block:after {
    right: 11px;
}
/*Rings in calendar*/
.calendar-block h3:before, .calendar-block h3:after {
    content: '';
    float: left;
    position: absolute;
    top: -5px;
    width: 4px;
    height: 14px;
    background: #dadada;
    background: -webkit-gradient(linear, left top, left bottom, from(#f1f1f1), to(#aaa));
    background: -moz-linear-gradient(top, #f1f1f1, #aaa);
    z-index: 2;
    border-radius: 2px;
}

.calendar-block h3:before {
    left: 13px;
}

.calendar-block h3:after {
    right: 13px;
}



/*STACKING CALENDAR DAY AS LIST EFFECT*/

.block-container::before,
.block-container::after {
    /*Add shadow to distinguish sheets from one another */
    box-shadow: 2px 1px 3px rgba(0, 0, 0, 0.2);
}

.block-container::before,
.block-container::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #eee;
    background: -webkit-linear-gradient(top, #dd1818 27%, #DDD 27%);
    background: linear-gradient(to bottom, #dd1818 27%, #DDD 27%);
}

/*Second sheet of paper */

.block-container::before {
    left: 5px;
    top: 5px;
    z-index: -1;
}

/*Third sheet of paper */

.block-container::after {
    left: 10px;
    top: 10px;
    z-index: -2;
}



@-webkit-keyframes calendarRoll {
    0% {
        opacity:1;
        -webkit-transform: rotateX(30deg);
                transform: rotateX(30deg);
    }
    25%{
        opacity:0.9;
        -webkit-transform:translate3d(0, -40px, 0)  rotateX(60deg);
                transform:translate3d(0, -40px, 0)  rotateX(60deg)
    }
    50% {
        opacity:0.7;
        -webkit-transform: translate3d(0, -105px, 0) rotateX(120deg);
                transform: translate3d(0, -105px, 0) rotateX(120deg)
    }
    75% {
        opacity:0.5;
        -webkit-transform: translate3d(0, -125px, 0) rotateX(130deg);
                transform: translate3d(0, -125px, 0) rotateX(130deg)
    }
    100% {
        opacity:0;
        -webkit-transform: translate3d(0, -125px, 0) rotateX(140deg);
                transform: translate3d(0, -125px, 0) rotateX(140deg)
    }
}



@keyframes calendarRoll {
    0% {
        opacity:1;
        -webkit-transform: rotateX(30deg);
                transform: rotateX(30deg);
    }
    25%{
        opacity:0.9;
        -webkit-transform:translate3d(0, -40px, 0)  rotateX(60deg);
                transform:translate3d(0, -40px, 0)  rotateX(60deg)
    }
    50% {
        opacity:0.7;
        -webkit-transform: translate3d(0, -105px, 0) rotateX(120deg);
                transform: translate3d(0, -105px, 0) rotateX(120deg)
    }
    75% {
        opacity:0.5;
        -webkit-transform: translate3d(0, -125px, 0) rotateX(130deg);
                transform: translate3d(0, -125px, 0) rotateX(130deg)
    }
    100% {
        opacity:0;
        -webkit-transform: translate3d(0, -125px, 0) rotateX(140deg);
                transform: translate3d(0, -125px, 0) rotateX(140deg)
    }
}

