}
.loading-wrapper{
    top:10%;
    position:relative;
    width:30px;
    height:30px;
    margin:auto;
}
.loading-inner{
    position:absolute;
    left:5px;
    top:5px;

    width:20px;
    height:20px;
}
.loading-wrapper:after{
    content: " ";
    display: block;
    width:30px;
    height:30px;
    border-radius: 50%;
    border: 5px solid #fff;
    border-color: #47e2ff teal;
