/*ON INVALID INPUT SHAKE MODAL ANIMATION*/
.shake-animate {
    -webkit-animation: shake .7s ease;
            animation: shake .7s ease;
}

@-webkit-keyframes shake {
    0% {
        -webkit-transform: rotate3d(1, 20, 2, 5deg);
                transform: rotate3d(1, 20, 2, 5deg);
    }
    25% {
        -webkit-transform: rotate3d(1, 20, 2, 5deg) rotateY(-15deg);
                transform: rotate3d(1, 20, 2, 5deg) rotateY(-15deg);
    }
    75% {
        -webkit-transform: rotate3d(1, 20, 2, 5deg) rotateY(15deg);
                transform: rotate3d(1, 20, 2, 5deg) rotateY(15deg);
    }
    100% {
        -webkit-transform: rotate3d(1, 20, 2, 5deg) rotateY(0deg);
                transform: rotate3d(1, 20, 2, 5deg) rotateY(0deg);

    }
}

@keyframes shake {
    0% {
        -webkit-transform: rotate3d(1, 20, 2, 5deg);
                transform: rotate3d(1, 20, 2, 5deg);
    }
    25% {
        -webkit-transform: rotate3d(1, 20, 2, 5deg) rotateY(-15deg);
                transform: rotate3d(1, 20, 2, 5deg) rotateY(-15deg);
    }
    75% {
        -webkit-transform: rotate3d(1, 20, 2, 5deg) rotateY(15deg);
                transform: rotate3d(1, 20, 2, 5deg) rotateY(15deg);
    }
    100% {
        -webkit-transform: rotate3d(1, 20, 2, 5deg) rotateY(0deg);
                transform: rotate3d(1, 20, 2, 5deg) rotateY(0deg);

    }
}
