
@media only screen and (max-width: 927px) {
    .calendar-container{
        flex-direction:column;
    }
    .calendar-container .body-container{
        width:100%;
    }
    .calendar-container .status-container{
        margin-bottom:15px;
        width:100%;
    }
    .view-note-container .notes-view{
        position:relative!important;
    }
    .user-form{
        font-size:0.95em!important;
    }
}
@media only screen and (max-width: 540px) {
    .calendar-body .day-block{
        flex:1 0 10%;
        font-size:0.7rem;
        min-width:15px;
    }
    .user-form{
        font-size:0.7em!important;
    }
}
